import AmountField from '../../../components/fields/AmountField';
import React, { useState } from 'react';
import { ValidationWrapperComponentProps } from '../../../components/RouteParamsValidationWrapper';
import { BigNumber, valueToBigNumber } from '@aave/protocol-js';
import { useProtocolDataContext } from '../../../libs/protocol-data-provider';
import { useUserWalletDataContext } from '../../../libs/web3-data-provider';
import ConnectButton from '../../../components/ConnectButton';
import DefaultButton from '../../../components/basic/DefaultButton';
import staticStyles from './DepositStyle';

interface WithdrawAmountProps
  extends Pick<
    ValidationWrapperComponentProps,
    'currencySymbol' | 'poolReserve' | 'walletBalance' | 'userReserve' | 'user'
  > {}

const Withdraw = (props: WithdrawAmountProps) => {
  const { walletBalance, poolReserve, currencySymbol, userReserve, user } = props;

  const { currentAccount } = useUserWalletDataContext();

  const [isMaxSelected, setIsMaxSelected] = useState(false);
  const [amount, setAmount] = useState('');
  const [error, setError] = useState('');

  if (!userReserve) {
    return null;
  }

  if (!user) {
    return null;
  }

  let maxUserAmountToWithdraw = BigNumber.min(
    userReserve.underlyingBalance,
    poolReserve.availableLiquidity
  ).toString(10);

  if (
    userReserve.usageAsCollateralEnabledOnUser &&
    poolReserve.usageAsCollateralEnabled &&
    user.totalBorrowsMarketReferenceCurrency !== '0'
  ) {
    // if we have any borrowings we should check how much we can withdraw without liquidation
    // with 0.5% gap to avoid reverting of tx
    let totalCollateralToWithdrawInETH = valueToBigNumber('0');
    const excessHF = valueToBigNumber(user.healthFactor).minus('1');
    if (excessHF.gt('0')) {
      totalCollateralToWithdrawInETH = excessHF
        .multipliedBy(user.totalBorrowsMarketReferenceCurrency)
        // because of the rounding issue on the contracts side this value still can be incorrect
        .div(Number(poolReserve.reserveLiquidationThreshold) + 0.01)
        .multipliedBy('0.99');
    }
    maxUserAmountToWithdraw = BigNumber.min(
      maxUserAmountToWithdraw,
      totalCollateralToWithdrawInETH.dividedBy(poolReserve.priceInMarketReferenceCurrency)
    ).toString();
  }
  maxUserAmountToWithdraw = BigNumber.max(maxUserAmountToWithdraw, 0).toString();

  let maxAmount = maxUserAmountToWithdraw;

  const handleAmountChange = (newAmount: string) => {
    const newAmountValue = valueToBigNumber(newAmount);
    setError('');
    if (newAmountValue.gt(maxAmount)) {
      setAmount(maxAmount);
      return setIsMaxSelected(true);
    } else if (newAmountValue.isNegative()) {
      setAmount('0');
    } else {
      setAmount(newAmount);
    }
    setIsMaxSelected(false);
  };

  const handleMaxButtonClick = () => {
    setAmount(maxAmount);
    setIsMaxSelected(true);
    setError('');
  };

  return (
    <div className={'material-primary Withdraw'}>
      <p className={'Withdraw__title'}>How much would you like to Withdraw?</p>
      <p className={'Withdraw__subtitle'}>
        Please enter an amount you would like to Withdraw. The maximum amount you can Withdraw is
        shown below.
      </p>

      <div className={'Withdraw__input'}>
        <AmountField
          title={'Available to withdraw'}
          maxAmount={maxAmount}
          symbol={currencySymbol}
          maxDecimals={poolReserve.decimals}
          value={amount}
          onChange={handleAmountChange}
          onMaxButtonClick={handleMaxButtonClick}
          error={error}
        />
      </div>
      <div className="Withdraw__buttons">
        {!currentAccount ? (
          <ConnectButton />
        ) : (
          <DefaultButton
            title={'Withdraw'}
            mobileBig={true}
            type="submit"
            onClick={() =>
              window.open(
                `/#/withdraw/${poolReserve.underlyingAsset}-${poolReserve.id}/confirmation?amount=${amount}`,
                '_self'
              )
            }
            disabled={maxAmount === '0' || Number(amount) === 0}
          />
        )}
      </div>

      <style jsx={true} global={true}>
        {staticStyles}
      </style>
    </div>
  );
};

export default Withdraw;
