import defaultMessages from '../../defaultMessages';
import ScreenWrapper from '../../components/wrappers/ScreenWrapper';
import { useIntl } from 'react-intl';
import staticStyles from './style';
import React from 'react';
import '../../theme/index.css';
import Deposit from './component/Deposit';
import Withdraw from './component/Withdraw';

import routeParamValidationHOC, {
  ValidationWrapperComponentProps,
} from '../../components/RouteParamsValidationWrapper';
import APRComponent from './component/APRComponent';
import { useDynamicPoolDataContext, useStaticPoolDataContext } from '../../libs/pool-data-provider';
import { valueToBigNumber } from '@aave/protocol-js';
import { BigNumber } from 'ethers';

interface EarnProps
  extends Pick<
    ValidationWrapperComponentProps,
    'currencySymbol' | 'poolReserve' | 'walletBalance' | 'userReserve' | 'user'
  > {}

function Earn({ currencySymbol, poolReserve, walletBalance, userReserve, user }: EarnProps) {
  const intl = useIntl();
  const { marketRefPriceInUsd } = useStaticPoolDataContext();
  const { reserves } = useDynamicPoolDataContext();
  let totalLockedInUsd = valueToBigNumber('0');
  let sortedData = reserves
    .filter((res) => res.isActive && !res.isFrozen)
    .map((reserve) => {
      totalLockedInUsd = totalLockedInUsd.plus(
        valueToBigNumber(reserve.totalLiquidity)
          .multipliedBy(reserve.priceInMarketReferenceCurrency)
          .multipliedBy(marketRefPriceInUsd)
      );
    });

  return (
    <ScreenWrapper
      pageTitle={intl.formatMessage(defaultMessages.earn)}
      subTitle={"Deposit your ARTH tokens and earn an interest from the lending protocol's usage"}
      isTitleOnDesktop={true}
      withMobileGrayBg={true}
    >
      <div className={'Earn'}>
        <div className={'Earn__left'}>
          <div className={'material-primary TotalDeposit'}>
            <div className={'single-line-center-between m-b-12'}>
              <span className={'label'}>Total Value Deposit: </span>
              <span className={'value'}>${totalLockedInUsd.toNumber().toFixed(2)}</span>
            </div>
            <div className={'single-line-center-between m-b-12'}>
              <span className={'label'}>Your ARTH deposit: </span>
              <span className={'value'}>
                {userReserve
                  ? `${userReserve.underlyingBalance} ${currencySymbol}`
                  : `- ${currencySymbol}`}
              </span>
            </div>
            <div className={'single-line-center-between'}>
              <span className={'label'}>{currencySymbol} Supply APR: </span>
              <span className={'value'}>{(Number(poolReserve.supplyAPR) * 100).toFixed(2)}%</span>
            </div>
          </div>
          <Deposit
            currencySymbol={currencySymbol}
            poolReserve={poolReserve}
            walletBalance={walletBalance}
          />
        </div>
        <div className={'Earn__right'}>
          <Withdraw
            currencySymbol={currencySymbol}
            poolReserve={poolReserve}
            walletBalance={walletBalance}
            userReserve={userReserve}
            user={user}
          />
          {/*<APRComponent />*/}
        </div>
      </div>

      <style jsx={true} global={true}>
        {staticStyles}
      </style>
    </ScreenWrapper>
  );
}

export default routeParamValidationHOC({
  withWalletBalance: true,
})(Earn);
